$arrow-width: 110%;
$arrow-height: 4px;
$arrow-color : black;

@mixin fontSize($sizeValue){
    font-size: $sizeValue * 1px;
    font-size: ($sizeValue/10) * 1rem;
}

button {
   outline: none;
   padding: 0;
}

.arrow, .arrowBack{
   box-sizing: inherit;
  // width:$arrow-width;
  height:$arrow-height;
  cursor:pointer;
  position:relative;
  // overflow:hidden;
  display:inline-block;
   // @include transition(all .5s ease);
 

  
  &:before {
    content: "";
    width:100%;
    height:50%;
    position:absolute;
    border-bottom:.7px solid black;
    top:1.3em;
    left:0;

  }
  
   &:after {
    content: "";
    width:$arrow-height;
    height:$arrow-height;
    position:absolute;
    border-top:.7px solid $arrow-color;
    border-right:.7px solid $arrow-color;
    top:1.3em;
    right:$arrow-height / 5;
    transform: rotate(45deg);
     // @include transition(border-color .5s ease);
     // z-index:9999;
  }
  
}

.arrowBack {
   
  
   &:after {
    content: "";
    width:$arrow-height;
    height:$arrow-height;
    position:absolute;
    border-top:.7px solid $arrow-color;
    border-right:.7px solid $arrow-color;
    top:1.3em;
    left:$arrow-height / 5;
    transform: rotate(-135deg);
     // @include transition(border-color .5s ease);
     // z-index:9999;
  }
  
}
.whiteText {
  -webkit-text-stroke: .7px black;
  color: white;
  font-weight: 700;
  &:hover {
    text-decoration: none;
  }
}
//exhibitors hi
.exhibitors {
  padding: 0;
  margin: 0;
  li{
    list-style: none;
    margin: 0;
    h1 {
      margin: 0;
      margin-bottom: 10px;
    }
      h1 a{
      text-decoration: none;
      // width: 100%;
      @include fontSize(17);
      -webkit-text-stroke: .7px black;
      color: white;
    } 

  }
}

.exhibitionSide h2 {
  @include fontSize(14);
  margin: 30px 0;
}
.exhibitionDateLoca h4 {
  margin: 0;
  @include fontSize(9);
}

//artfairs
h2.artFairTitle {
  @extend .whiteText;
  @include fontSize(17);
  margin: 5px 0;
}
.artFairDates {
  font-weight: 700;
  @include fontSize(8);
  p {
    margin: 0;
  }
}
.artFairArtists{
  h4 {
    margin: 40px 0 10px;
    @extend .whiteText;
    @include fontSize(14);
  }
}
.artFairExhibitors {
    list-style: none;
    margin: 0;
    padding: 0;
    li {
      margin-bottom: 10px;
      a {
        @extend .arrow;
        color: black;
        font-weight:700;
        text-transform: uppercase;



      }
    }
    

}

